.slider__wrapper {
  position: relative; }

.slide_second-background {
  background-color: #648BF0;
  border-bottom: 6px solid #3a4ec9; }

.slider-show {
  position: absolute;
  width: 1020px;
  height: 594px;
  overflow: hidden;
  z-index: 1;
  user-select: none; }

.slide {
  width: 1020px;
  height: 594px;
  position: absolute;
  top: 0;
  left: 0;
  transition: all ease 1s; }

#slide_second img {
  position: absolute;
  top: 50px;
  left: 250px; }

.horizont-mobile {
  margin: 0 0 122px 69px; }

.vertical-mobile {
  margin: 53px 0 0 112px; }

.vertical_mobile-black {
